const elapsedTimeCalc = (comparedDate) => {
  const currentDate = new Date();
  const specificDate = new Date(comparedDate);

  const timeDifference = currentDate - specificDate;

  const min = 1000 * 60;
  const hour = min * 60;
  const day = hour * 24;
  const week = day * 7;

  if (timeDifference >= week) {
    return `${Math.floor(timeDifference / week)} `;
  } else if (timeDifference >= day) {
    return `${Math.floor(timeDifference / day)} `;
  } else if (timeDifference >= hour) {
    return `${Math.floor(timeDifference / hour)} `;
  } else if (timeDifference >= min) {
    return `${Math.floor(timeDifference / min)} `;
  } else {
    return ` `;
  }
};

export default elapsedTimeCalc;
